import React from "react";
import {PropTypes} from "prop-types";

import ConvergentPropositionEntry from "./propositionEntries/ConvergentPropositionEntry"
import ConvergentProductEntries from "./productEntries/ConvergentProductEntries"
import {ConvergentMultiCartExpandedItemDescriptionsType} from "./typings";
import ProcessTypeEnum from "../../../../magnum2/constants/ProcessTypeEnum";
import {getProcessType} from "../../../../magnum2/utils";

ConvergentMultiCartExpandedItemComponent.propTypes = {
    isSatTechnology: PropTypes.bool,
    isDslTechnology: PropTypes.bool,
    sectionConf: PropTypes.any,
    entry: PropTypes.any,
    descriptions: PropTypes.shape(ConvergentMultiCartExpandedItemDescriptionsType),
    onDeviceChange: PropTypes.func,
    onRemoveProduct: PropTypes.func,
    lowerInstallmentsClicked: PropTypes.func,
    onRemoveTerminal: PropTypes.func,
    onClickAddVas: PropTypes.func,
    onClickAddTvPackage: PropTypes.func,
    onClickAddFixBonus: PropTypes.func,
    areSecondaryChoiceBonusesAvailable: PropTypes.bool
};

function ConvergentMultiCartExpandedItemComponent(props) {
    const {isSatTechnology, isDslTechnology, descriptions, sectionConf, entry, configurables, showNetPrices} = props;
    const {onDeviceChange, onRemoveProduct, onRemoveTerminal, onClickAddVas, onClickAddTvPackage, onClickAddFixBonus, areSecondaryChoiceBonusesAvailable,lowerInstallmentsClicked} = props;
    const processType = getProcessType(entry.entries);

    return (
        <div
            aria-hidden="false"
            style={{display: 'block'}}
            className="opl-checkout__section__content"
            id="mod-core/modules/expander-3-tab-0"
            role="tabpanel"
            aria-labelledby="mod-core/modules/expander-3-control-0"
        >
            <div className="l-full-row">
                <div className="l-page-row">
                    <div className="u-no-padding">
                        <ConvergentPropositionEntry
                            descriptions={descriptions}
                            sectionConf={sectionConf}
                            entry={entry}
                            openRemovePopup={props.openRemovePopup}
                            configurables={configurables}
                            openVasModal={onClickAddVas}
                            showNetPrices={showNetPrices}
                            onRemoveProduct={onRemoveProduct}
                        />
                        {isSatTechnology && (
                            <div className="u-padding-m u-padding-top-s">
                                <p className="u-font-small" dangerouslySetInnerHTML={{__html: descriptions.convergentMultiCartLegalSAT}} />
                            </div>
                        )}
                        {processType === ProcessTypeEnum._2U && isDslTechnology && (
                            <div className="u-padding-m u-padding-top-s">
                                <p className="u-font-small" dangerouslySetInnerHTML={{__html: descriptions.convergentMultiCartLegalDSL}} />
                            </div>
                        )}
                    </div>
                </div>
                <ConvergentProductEntries
                    sectionConf={sectionConf}
                    entry={entry}
                    lowerInstallmentsClicked={lowerInstallmentsClicked}
                    onDeviceChange={onDeviceChange}
                    onRemoveProduct={onRemoveProduct}
                    onRemoveTerminal={onRemoveTerminal}
                    onClickAddVas={onClickAddVas}
                    onClickAddTvPackage={onClickAddTvPackage}
                    onClickAddFixBonus={onClickAddFixBonus}
                    showNetPrices={showNetPrices}
                    areSecondaryChoiceBonusesAvailable={areSecondaryChoiceBonusesAvailable}
                />
            </div>
        </div>
    );
}

export default ConvergentMultiCartExpandedItemComponent;